@-webkit-keyframes pulse {
  0% {
    width: 0%;
    height: 0%;
    opacity: 1;}
  90% {
    width: 100%;
    height: 100%;
    opacity: 0; } }
@keyframes pulse {
  0% {
    width: 0%;
    height: 0%;
    opacity: 1;}
  90% {
    width: 100%;
    height: 100%;
    opacity: 0; } }



