@-webkit-keyframes dots {
  0% {
    width: 0%;
    height: 0%; }
  50% {
    width: 100%;
    height: 100%;  }
  100% {
    width: 0%;
    height: 0%;  } }
@keyframes dots {
  0% {
    width: 0%;
    height: 0%; }
  50% {
    width: 100%;
    height: 100%;  }
  100% {
    width: 0%;
    height: 0%;  } }



